<template>
  <div class="dark:text-white dark:bg-gray-800">
    <div class="py-14 container">
      <h1 class="text-center text-4xl font-semibold mb-5">{{projectDetail.title}}</h1>
      <div v-if="projectDetail.image.length == 1" class="lg:w-1/2 m-auto mb-5 rounded overflow-hidden bg-gray-100 dark:bg-gray-600">
        <img v-lazy="getImgUrl(projectDetail.image)" alt="{{projectDetail.image}}" class="p-14">
      </div>
      <div v-else class="lg:w-1/2 h-96 md:h-110 lg:h-100 m-auto mb-5 rounded overflow-hidden bg-gray-100 dark:bg-gray-600">
        <Splide :options="{ rewind: true, type: 'fade', drag: true, fixedHeight: '600px'}">
          <template v-for="(image, index) in projectDetail.image" :key="index">
            <SplideSlide>
              <img :src="getImgUrl(image)" alt="{{image}}" class="p-2 md:p-14 object-contain w-full h-96 md:h-full">
            </SplideSlide>
          </template>
        </Splide>
      </div>
      <div class="md:w-1/2 m-auto">
        <div class="flex flex-wrap">
          <template v-for="(tag, index) in projectDetail.tag" :key="index">
            <span @click="$router.push({name: 'tag', query: { q: tag }})" class="font-normal m-1 bg-gray-200 hover:bg-gray-300 rounded-full px-2 text-sm leading-loose cursor-pointer transition-colors duration-300 dark:bg-gray-600 dark:hover:bg-gray-500">
              {{tag}}
            </span>
          </template>
        </div>
        <p class="mb-5">{{projectDetail.description}}</p>

        <div class="block lg:flex">
          <button @click="$router.go(-1)" class="px-4 py-2 mb-2 mx-2 lg:my-0 rounded-full shadow-md text-white border-2 border-indigo-500 bg-indigo-500 hover:bg-transparent hover:text-indigo-500 focus:bg-transparent focus:text-indigo-500 transition-colors duration-300 dark:bg-indigo-700 dark:border-indigo-700 dark:hover:bg-transparent dark:hover:text-indigo-600 dark:focus:bg-transparent dark:focus:text-indigo-600">&lArr; Go Back</button>
          <a v-if="projectDetail.github" :href="projectDetail.github" target="_blank" class=" px-4 py-2 flex items-center border shadow-md transform focus:scale-95 focus:shadow-none transition duration-200 justify-center rounded-full dark:border-gray-600 mx-2">
            Read More on Github&nbsp;
            <img v-if="mode == 'light'" src="../assets/GitHub-Mark-64px.png" alt="github icon" class="h-8 opacity-75">
            <img v-else src="../assets/GitHub-Mark-Light-64px.png" alt="github icon" class="h-8 opacity-75">
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Splide, SplideSlide } from "@splidejs/vue-splide";
import "@splidejs/splide/dist/css/splide.min.css";
import { useMeta } from 'vue-meta';
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

export default {
  components: {
    Splide,
    SplideSlide,
  },
  setup () {
    useMeta({ title: useStore().getters.detailProject(useRoute().params.slug).title })
  },
  computed: {
    projectDetail(){
      return this.$store.getters.detailProject(this.$route.params.slug)
    },
    mode(){
      return this.$store.getters.mode
    }
  },
  methods: {
    getImgUrl(pic) {
      return require('../assets/project-foto/'+pic)
    },
    nl2br (str, replaceMode = false, isXhtml = false) {
      var breakTag = (isXhtml) ? '<br />' : '<br>';
      var replaceStr = (replaceMode) ? '$1'+ breakTag : '$1'+ breakTag +'$2';
      return (str + '').replace(/([^>\r\n]?)(\r\n|\n\r|\r|\n)/g, replaceStr);
    }
  }
}
</script>

